import { Component } from '@angular/core';
import { IonicPage, ViewController } from 'ionic-angular';

@IonicPage({
  name: 'HowToAddBookPage',
  segment: 'how-to-add-book'
})
@Component({
  selector: 'page-how-to-add-book',
  templateUrl: 'how-to-add-book.html'
})
export class HowToAddBookPage {

  constructor(public viewCtrl: ViewController) {

  }

  selectBookFromAmazon(event: Event) {
    this.viewCtrl.dismiss('amazon');
  }

  addBookByHand(event: Event) {
    this.viewCtrl.dismiss('hand');
  }
}



